import { Component, Input, EventEmitter, Output, AfterViewInit, OnInit } from '@angular/core';
import { BcCardPrimaryConfig, BcFloatMenuBehavior, CardPrimaryButton, CardPrimaryIconButton, CardPrimaryInfoAccount, CardPrimaryInfoData, CardPrimaryStatus, CardPrimaryTag } from '@bancolombia/design-system-behaviors';

/**
 * Componente Card Select
 *
 * @export
 * @class BcCardSelectComponent
 * @implements {OnInit}
 * @implements {AfterViewInit}
 */
@Component({
  selector: 'bc-card-primary',
  templateUrl: 'bc-card-primary.component.html',
})
export class BcCardPrimaryComponent implements AfterViewInit, OnInit{

  infoAccount: CardPrimaryInfoAccount;
  dataOne: CardPrimaryInfoData;
  dataTwo: CardPrimaryInfoData;
  componentStatus: CardPrimaryStatus;
  componentTagOne: CardPrimaryTag;
  componentTagTwo: CardPrimaryTag;
  componentButton: CardPrimaryButton;
  componentIconButton: CardPrimaryIconButton;


  /**
   * Par√°metro para denotar el tipo de tag
   * @type BcCardPrimaryConfig
   * @memberof BcCardPrimaryComponent
   */
  @Input() dataConfig: BcCardPrimaryConfig;
   /**
   * Salida de evento del componente Tag.
   *
   * @type {(EventEmitter<any>)}
   * @memberof BcCardPrimaryComponent
   */
  @Output() handleEvent: EventEmitter<any> = new EventEmitter<any>();

  ngOnInit(): void {
    this.infoAccount = this.dataConfig.infoAccount;
    this.dataOne = this.dataConfig.dataOne;
    this.dataTwo = this.dataConfig.dataTwo;
    this.componentStatus = this.dataConfig.componentStatus;
    this.componentTagOne = this.dataConfig.componentTagOne;
    this.componentTagTwo = this.dataConfig.componentTagTwo;
    this.componentButton = this.dataConfig.componentButton;
    this.componentIconButton = this.dataConfig.componentIconButton;

  }

  ngAfterViewInit(): void {
    BcFloatMenuBehavior.setUpAll();
  }

  propagateEvent=(id: string) =>{
    this.handleEvent.emit(id);
  }
}
